/* http://meyerweb.com/eric/tools/css/reset/
v2.0 | 20110126
License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
hr {
  outline: none;
  border-color: black;
}

// Generals

:root {
  // colors
  --yellow: #fcba28;
  --purple: #7b5ea7;
  --blue: #1c9bf0;
  --green: #238636;
  --pink: #f38ba3;
  --red: #ed203d;
  --orange: #fc7427;
  --ig: #db00ff;
  --off: rgb(249, 244, 218);
  --pale-off: rgb(244, 237, 211);
  --black: rgb(24, 24, 24);
  --pale-black: rgb(31, 31, 31);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.grid {
  display: grid;
}

.container {
  padding-inline: 4rem;
}

// Body

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  letter-spacing: 0.01rem;
  line-height: 1.4;
  word-spacing: 1px;
  background-color: var(--black);
}

a {
  text-decoration: none;
  color: var(--off);
}

.light-mode {
  background-color: var(--off);
  color: var(--black);
  transition: background-color 1s ease-in-out;

  a {
    color: var(--black);
  }
}

.dark-mode {
  background-color: var(--black);
  color: var(--off);
  transition: background-color 1s ease-in-out;

  a {
    color: var(--off);
  }
}

.heading {
  font-size: 2rem;
  font-weight: 600;
}
